.card{
    position:relative;
    width: 340px;
    height:340px;
    border-radius: 10px;
    margin: 25px;
    cursor: pointer;
}

.card:hover .shadow{
    cursor: pointer;
    box-shadow: 2px 2px 10px #ff6060;
    opacity : 0.5;
    
    
}

.card .shadow{
    background: linear-gradient(180deg,#ff6060 85%,#000000a6);
    border-radius: 10px;
    height: 100%;
    width: 100%;
    opacity:0;
    position: absolute;
    top: 0;
    left: 0;
    transition: all 300ms ease-out;
}

.cover{
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.title{
    position: absolute;
    bottom : 20px;
    left:20px;
    color: white;
    font-size: 18px;
    font-weight: 500;
}

@media only screen and (max-width: 375px){
    .card{
        position:relative;
        width: 335px;
        height:255px;
        margin: 0px 20px 20px;
    }
    
}